<template>
  <div id="app">
    <Navbar/>
    <div class="container">
    <router-view/>
    <div @click="toggleClicked" v-bind:class="{ 'clicked': languageToggleClicked }">
    <button v-for="entry in languages" :key="entry.title" @click="changeLocale(entry.language)">
        <flag :iso="entry.flag" v-bind:squared=false /> {{entry.title}}
    </button>
    </div>
    </div>
  </div>
</template>

<script>
import Navbar from './components/Navbar'
import i18n from '@/plugins/i18n';
export default {
  name: 'App',
  components: {
    Navbar
  },
  data() {
    return {
      languageToggleClicked: false,
      languages: [
            { flag: 'us', language: 'en', title: 'English' },
            { flag: 'ru', language: 'ru', title: 'Русский' }
        ]
    }
  },
  methods: {
    changeLocale(locale) {
        i18n.locale = locale;
    },
    toggleClicked() {
      this.languageToggleClicked = true
    }
}
}
</script>
<style>
button {
    padding: 15px;
    border: 1px solid green;
    font-size: 18px;
    margin: 15px;
    cursor: pointer;
}
.clicked {
  display: none;
}
</style>



