import Vue from 'vue';
import VueI18n from 'vue-i18n';

Vue.use(VueI18n);
const messages = {
    'en': {
    	employees: "Employees",
    	navbar: "Employee Manager",
    	joinButton: "Join us"
    },
    'ru': {
    	employees: "Сотрудники",
    	navbar: "Менеджер Сотрудников",
    	joinButton: "Стать одним из нас"
    }
};
const i18n = new VueI18n({
    locale: 'en', // set locale
    fallbackLocale: 'ru', // set fallback locale
    messages, // set locale messages
});
export default i18n


